'use client';

import { useQuery } from "@tanstack/react-query";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import Link from "next/link";
import type { Course } from "@shared/schema";

export default function CoursesSection() {
  // ✅ Fetch only featured diploma courses (limit 6 for homepage)
  const {
    data: diplomaCourses,
    isLoading: isDiplomaLoading,
    error: diplomaError,
  } = useQuery<Course[]>({
    queryKey: ["/api/courses", { type: "diploma", featured: "true", limit: "6" }],
    queryFn: async () => {
      const response = await fetch("/api/courses?type=diploma&featured=true&limit=6");
      if (!response.ok) throw new Error("Failed to fetch featured diploma courses");
      return response.json();
    },
  });

  // ✅ Fetch only featured certificate courses (limit 6 for homepage)
  const {
    data: certificateCourses,
    isLoading: isCertificateLoading,
    error: certificateError,
  } = useQuery<Course[]>({
    queryKey: ["/api/courses", { type: "certificate", featured: "true", limit: "6" }],
    queryFn: async () => {
      const response = await fetch("/api/courses?type=certificate&featured=true&limit=6");
      if (!response.ok) throw new Error("Failed to fetch featured certificate courses");
      return response.json();
    },
  });

  const getIcon = (title: string) => {
    if (title.includes("Mechanical") || title.includes("Electrical")) return "fas fa-cog";
    if (title.includes("Information") || title.includes("Computer") || title.includes("ICT")) return "fas fa-laptop-code";
    if (title.includes("Business") || title.includes("Office") || title.includes("Human Resource")) return "fas fa-chart-line";
    if (title.includes("Hotel") || title.includes("Hospitality") || title.includes("Food")) return "fas fa-utensils";
    if (title.includes("Nursing") || title.includes("Health")) return "fas fa-heartbeat";
    if (title.includes("Motor") || title.includes("Automotive")) return "fas fa-wrench";
    if (title.includes("Construction") || title.includes("Carpentry") || title.includes("Civil") || title.includes("Building")) return "fas fa-hammer";
    if (title.includes("Fashion")) return "fas fa-cut";
    if (title.includes("Agriculture") || title.includes("Horticulture")) return "fas fa-seedling";
    if (title.includes("Beauty") || title.includes("Hairdressing")) return "fas fa-paint-brush";
    if (title.includes("Plumbing")) return "fas fa-wrench";
    if (title.includes("Survey")) return "fas fa-map";
    if (title.includes("Social Work") || title.includes("Community")) return "fas fa-hands-helping";
    return "fas fa-graduation-cap";
  };

  const getIconColor = (index: number) => {
    const colors = ["bg-primary", "bg-accent", "bg-success", "bg-warning", "bg-error", "bg-secondary"];
    return colors[index % colors.length];
  };

  // ✅ Error fallback
  if (diplomaError || certificateError) {
    return (
      <section className="py-16 bg-gray-50 text-center">
        <h2 className="text-2xl font-bold text-red-600 mb-4">Error Loading Courses</h2>
        <p className="text-gray-600">Please check your connection or try again later.</p>
      </section>
    );
  }

  // ✅ Loading fallback
  if (isDiplomaLoading || isCertificateLoading) {
    return (
      <section className="py-8 bg-gray-50">
        <div className="container mx-auto px-4">
          <div className="text-center mb-8">
            <Skeleton className="h-10 w-64 mx-auto mb-4" />
            <Skeleton className="h-6 w-96 mx-auto" />
          </div>
          <div className="mb-8">
            <Skeleton className="h-8 w-48 mx-auto mb-6" />
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {[...Array(6)].map((_, i) => (
                <Card key={i} className="p-6">
                  <div className="flex items-start space-x-4">
                    <Skeleton className="w-12 h-12 rounded-lg" />
                    <div className="flex-1">
                      <Skeleton className="h-6 w-full mb-2" />
                      <Skeleton className="h-4 w-full mb-3" />
                      <div className="flex items-center justify-between">
                        <Skeleton className="h-4 w-16" />
                        <Skeleton className="h-4 w-20" />
                      </div>
                    </div>
                  </div>
                </Card>
              ))}
            </div>
          </div>
          <div>
            <Skeleton className="h-8 w-48 mx-auto mb-6" />
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {[...Array(6)].map((_, i) => (
                <Card key={i} className="p-6">
                  <div className="flex items-start space-x-4">
                    <Skeleton className="w-12 h-12 rounded-lg" />
                    <div className="flex-1">
                      <Skeleton className="h-6 w-full mb-2" />
                      <Skeleton className="h-4 w-full mb-3" />
                      <div className="flex items-center justify-between">
                        <Skeleton className="h-4 w-16" />
                        <Skeleton className="h-4 w-20" />
                      </div>
                    </div>
                  </div>
                </Card>
              ))}
            </div>
          </div>
        </div>
      </section>
    );
  }

  // ✅ Success state - Display featured courses only
  return (
    <section id="academics" className="py-8 bg-gray-50">
      <div className="container mx-auto px-4">
        <div className="text-center mb-8">
          <h2 className="text-4xl font-bold text-gray-800 mb-4">
            Popular Courses
          </h2>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            Discover our most sought-after diploma and certificate programs designed to launch your career
          </p>
        </div>

        {/* Diploma Programs - Only show if we have featured courses */}
        {diplomaCourses && diplomaCourses.length > 0 && (
          <div className="mb-8">
            <div className="flex items-center justify-between mb-6">
              <h3 className="text-2xl font-bold text-gray-800">
                Featured Diploma Programs
              </h3>
              <Link href="/courses/diploma">
                <Button variant="outline" size="sm" className="text-primary border-primary hover:bg-primary hover:text-white">
                  View All Diplomas
                </Button>
              </Link>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {diplomaCourses.map((course, index) => (
                <Card key={course.id} className="p-6 hover:shadow-xl transition-shadow border-l-4 border-l-blue-500">
                  <div className="flex items-start space-x-4">
                    <div className={`w-12 h-12 ${getIconColor(index)} rounded-lg flex items-center justify-center flex-shrink-0`}>
                      <i className={`${getIcon(course.title)} text-white`}></i>
                    </div>
                    <div className="flex-1">
                      <h4 className="text-lg font-semibold text-gray-800 mb-2 line-clamp-2">
                        {course.title}
                      </h4>
                      <p className="text-gray-600 text-sm mb-3 line-clamp-2">
                        {course.description}
                      </p>
                      <div className="flex items-center justify-between">
                        <span className="text-primary font-semibold">
                          {course.duration}
                        </span>
                        <Link href={`/courses/${course.id}`}>
                          <Button variant="link" className="text-primary hover:text-secondary font-medium p-0">
                            Learn More
                          </Button>
                        </Link>
                      </div>
                    </div>
                  </div>
                </Card>
              ))}
            </div>
          </div>
        )}

        {/* Certificate Programs - Only show if we have featured courses */}
        {certificateCourses && certificateCourses.length > 0 && (
          <div className="mb-6">
            <div className="flex items-center justify-between mb-6">
              <h3 className="text-2xl font-bold text-gray-800">
                Featured Certificate Programs
              </h3>
              <Link href="/courses/certificate">
                <Button variant="outline" size="sm" className="text-primary border-primary hover:bg-primary hover:text-white">
                  View All Certificates
                </Button>
              </Link>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {certificateCourses.map((course, index) => (
                <Card key={course.id} className="p-6 hover:shadow-xl transition-shadow border-l-4 border-l-green-500">
                  <div className="flex items-start space-x-4">
                    <div className={`w-12 h-12 ${getIconColor(index)} rounded-lg flex items-center justify-center flex-shrink-0`}>
                      <i className={`${getIcon(course.title)} text-white`}></i>
                    </div>
                    <div className="flex-1">
                      <h4 className="text-lg font-semibold text-gray-800 mb-2 line-clamp-2">
                        {course.title}
                      </h4>
                      <p className="text-gray-600 text-sm mb-3 line-clamp-2">
                        {course.description}
                      </p>
                      <div className="flex items-center justify-between">
                        <span className="text-primary font-semibold">
                          {course.duration}
                        </span>
                        <Link href={`/courses/${course.id}`}>
                          <Button variant="link" className="text-primary hover:text-secondary font-medium p-0">
                            Learn More
                          </Button>
                        </Link>
                      </div>
                    </div>
                  </div>
                </Card>
              ))}
            </div>
          </div>
        )}

        {/* Call to Action */}
        <div className="text-center mt-4">
          <Link href="/courses">
            <Button size="lg" className="bg-primary hover:bg-primary/90 text-white px-8 py-3">
              Explore All Our Courses
            </Button>
          </Link>
        </div>
      </div>
    </section>
  );
}